<template>
        <v-toolbar class="indigo lighten-1 navbar-container" flat fixed>
      <v-toolbar-title class="hidden-sm-and-down navbar-content"><router-link :to="{ name: 'index' }">devBook</router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="navbar-content">
        <v-btn :to="{ name: 'index' }" exact flat v-if="!$store.state.authUser">Home</v-btn>
        <v-btn :to="{ name: 'myprofile', params: {curr_id: authUserID}}" flat v-if="$store.state.authUser">My Profile</v-btn>
        <v-btn :to="{ name: 'users' }" flat>Users</v-btn>
        <v-btn @click="signOut" v-if="$store.state.authUser" class="text-capitalize white--text" flat>Logout</v-btn>
      </v-toolbar-items>
    </v-toolbar>
</template>

<script>
import firebase from 'firebase'
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

export default {
  data() {
      return {
          authUserID: null
      }
  },

  
  methods: {
    signOut() {
      NProgress.start()
      firebase.auth().signOut().then(() => {
        this.$router.push({ name: 'login' })
        NProgress.done()
      })
    }
  },

  beforeCreate() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.$store.state.authUser = true
        this.authUserID = user.uid
      } else {
        this.$store.state.authUser = false
      }
    })
  }
}
</script>
